@import '../../../assets/css/theme';
@import '../../../assets/css/var';

.info_box {
  background: $color-info-bg;
  border-radius: 10px;
  border: 1px solid $color-border-secondary;
  display: flex;
  align-items: center;
  padding: 24px;
  &_v {
    flex-direction: column;
  }
  &_no_border {
    border: none;
  }
  &_item {
    flex: 1;
    text-align: center;
    position: relative;
    &:after {
      width: 1px;
      height: 80%;
      top: 50%;
      transform: translateY(-50%);
      position: absolute;
      right: 0;
      content: '';
      background-color: $color-border-secondary;
    }
  }
  &_item:last-child {
    &:after {
      display: none;
    }
  }
  &_label {
    font-size: 12px;
    font-weight: 600;
    color: $color-text-default-5;
    line-height: 17px;
  }
  &_value {
    font-size: 16px;
    font-weight: 600;
    color: $color-text-default;
    line-height: 22px;
    margin-top: 10px;
    word-break: break-all;
  }
}
.info_box_v {
  align-items: stretch;
  .info_box_item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
    &:last-child {
      margin-bottom: 0;
    }
    &:after {
      display: none;
    }
  }
  .info_box_value {
    margin: 0;
  }
}
@include mobile-only {
  .info_box {
    padding: 12px;
  }
  .info_box_responsive {
    flex-direction: column;
    justify-content: flex-start;
    .info_box_item {
      text-align: left;
      width: 100%;
      margin-bottom: 18px;
      &:last-child {
        margin-bottom: 0;
      }
      &:after {
        display: none;
      }
    }
    &_h {
      .info_box_item {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
    }
  }
}
